<template>
    <div class="header">
        <div class="content page-width">
            <div class="logos">
                <img alt="logo" :src="info.logo_url" class="logo"/>
                <h3>{{ info.company_name }}</h3>
            </div>
            <div class="navigate">
                <ul>
                    <li v-for="item in info.routes" @click="navigate(item.path)" :key="item.key" :class="{ 'li-check':route.path === item.path }">
                        {{ item.name }}
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>
<script setup>
    import { computed } from "vue";
    import { useStore } from 'vuex';
    import { useRouter,useRoute } from "vue-router";
    const store = useStore();
    const info = computed(() => store.state.info);
    const route = useRoute()
    const routes = useRouter()
    const navigate = (value) => {
        routes.push(value)
    }
</script>
