<!-- SinglePage.vue -->
<template>
  <div>
    <h1>{{title}}</h1>
    <div v-html="content"></div>
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  name: 'SinglePage',
  computed: {
    ...mapState([
      'info'
    ]),
    title() {
      return this.$route.name || '';
    },
    content() {
      return this.info[this.$route.meta.key] || '';
    }
  },
}
</script>

