<template>
    <img src="../../assets/about.png" class="about-img" alt="关于我们" />
    <div class="content page-width">
        <div class="header">
            <div style="display:flex;align-items: center;cursor: pointer;" @click="navigate('/')">
                <img src="../../assets/home.png" class="home-icon"/>
                <span>首页</span>
            </div>
            <span style="margin:0px 10px;"> > </span>
            <span>产品</span>
        </div>
        <div class="row">
            <div class="left">
                <div class="left-head">
                    <span class="left-name">公司产品</span>
                    <span class="left-en">product</span>
                </div>
                <div class="left-content" @click="changeCurrent('product')">
                    <img v-if="current === 'product'" src="../../assets/right-check-icon.png" class="right-icon"/>
                    <img v-else src="../../assets/right-icon.png" class="right-icon"/>
                    <span>企业产品</span>
                </div>
            </div>
            <div class="right">
                <div v-html="htmls"></div>
            </div>
        </div>
    </div>
</template>
<script setup>
    import { useRouter } from 'vue-router';
    import { computed, ref } from "vue";
    import { useStore } from 'vuex';

    const store = useStore();
    const info = computed(() => store.state.info);
    const route = useRouter();
    let current = ref('product')
    let htmls = ref(info.value.introduction?.content);
    const navigate = (value) => {
        route.push(value)
    }

    const changeCurrent = (value) => {
        current.value = value;
        if (value === 'introduction') {
            htmls.value = info.value[value].content
        } else {
            htmls.value = info.value[value]
        }
    }

</script>
