// store.js
import { createStore } from 'vuex';
import axios from 'axios';

export default createStore({
  state: {
    info: ''
  },
  mutations: {
    setInfo(state, info) {
      state.info = info;
    }
  },
  actions: {
    async fetchInfo({ commit }) {
      let websiteData = {};
      try {
          const domain = window.location.host;
          const url = 'http://weka-ai-website-api.ai-site.cc/api/get-website-data?domain=' + domain;
          const response = await axios.get(url);
          const responseData = response.data;
          websiteData = JSON.parse(responseData.resp_data);
          websiteData.routes = [
          { path: '/', name: '首页', key: 'home' },
          { path: '/about', name: '关于我们', key: 'introduction' },
          { path: '/product', name: '产品', key: 'product' }
        ];
        if (websiteData.introduction) {
          websiteData.introduction = JSON.parse(websiteData.introduction)
        }
        commit('setInfo', websiteData);
      } catch (error) {
        console.error(error);
      }
    }
  }
});